import base from './base'; // 
import Request from '../request'; // httpaxios
let axios = new Request()
const loading = base.loading
//  get
function conversion(params){
	let str = '?'
	for(let key in params){
		str += `${key}=${params[key]}&`
	}
	return str.slice(0,-1)
}


const login = {
	// 
	terminalLogin(params, options) {
		return axios.post(`${base.api}/auth/terminalLogin`, params, options,loading);
	},
	// websocket 
	getWebsocketBackupUrl(params, options) {
		return axios.get(`${base.api}/configuration/getWebsocketBackupUrl`, params, options,loading);
	},
	// 
	selectAcquirerStoreDtoWithAcquirer(params, options) {
		return axios.get(`${base.api}/acquirerStore/selectAcquirerStoreDtoWithAcquirer`, params, options,loading);
	},
	//   	?creationTimeStart=2023-08-15 00:00:00&creationTimeEnd=2023-08-20 00:00:00
	pageTransactionAndTransactionMetadata(params, options) {
		console.log(conversion(params))
		return axios.get(`${base.api}/transactionAndTransactionMetadata/pageTransactionAndTransactionMetadata`+conversion(params), params, options,loading);
	},
	//   	?printTypeFlag=terminal&creationTimeStart=2023-08-15 00:00:00&creationTimeEnd=2023-08-20 00:00:00
	//  transactionAndTransactionMetadata/printReceiptSummaryByDate
	printReceiptSummaryByDate(params, options) {
		return axios.get(`${base.api}/transactionAndTransactionMetadata/printReceiptSummaryByDate/v1`+conversion(params), params, options,loading);
	},
	//   
	paymentPay(params, options) {
		return axios.post(`${base.api}/uniweb/paymentPay`, params, options,loading);
	},
	// 
	validateRefundPassword(params, options) {
		return axios.get(`${base.api}/store/validateRefundPassword`+conversion(params), params, options,loading);
	},
	get_upgrade(params, options){
		return axios.get(`http://18.140.198.86/pos/get_upgrade.php`+conversion(params), params, options,loading);
	},
	// token storeName
	storeInfo(params, options) {
		return axios.get(`${base.api}/store/storeInfo`+conversion(params), params, options,loading);
	},
	// token storeName  https://app.uniwebpay.com/app-rest/transactionAndTransactionMetadata/selectTransactionById?id=1226956119910776832
	selectTransactionById(params, options) {
		return axios.get(`${base.api}/transactionAndTransactionMetadata/selectTransactionById`+conversion(params), params, options,loading);
	},
};

export default {
	...login
};
